<template>
  <v-container overflow-hidden fill-height class="folks_med" fluid>
    <v-layout outline row wrap>
      <v-flex xs12 hidden-sm-and-up style="height:8%;"
        >filler for mobile alignment</v-flex
      >
      <v-flex xs12 text-xs-left mt2>
        <h1 style="color:#376C88;font-weight:bold;">{{ PageTitle }}</h1>
        <v-layout v-for="cat in disp" :key="cat.title">
          <v-flex xs12>
            <v-card flat tile color="transparent">
              <v-card-title
                style="color:#518BAA;font-size:1.3em;font-weight:bold;"
                >{{ cat.title }}</v-card-title
              >
              <v-container pa-0 grid-list-md fluid>
                <v-layout row wrap>
                  <v-flex
                    v-for="subcat in cat.sublinks"
                    :key="subcat.title"
                    xs12
                    sm3
                  >
                    <v-card
                      :color="cardColor"
                      flat
                      hover
                      :href="subcat.url"
                      target="_blank"
                      style="border:2px solid white;"
                    >
                      <v-card-text class="pa-1">
                        <v-layout row wrap class="folks_tiles">
                          <v-flex xs12 text-xs-center align-center>
                            <v-img
                              contain
                              height="50"
                              aspect-ratio="1.7"
                              :src="publicPath + subcat.logo"
                            ></v-img>
                          </v-flex>
                          <v-flex xs10 text-xs-left>
                            <v-layout column>
                              <v-flex style="font-weight:bolder;">
                                <b>
                                  <u>{{ subcat.title }}</u>
                                </b>
                              </v-flex>
                              <v-flex style="line-height: .5;">
                                <small>
                                  <v-chip
                                    v-for="chip in showChips()"
                                    :key="chip.info"
                                    small
                                    pa-0
                                    ma-0
                                    style="font-size:.8em;font-weight:bold;margin:0;"
                                    :color="chip.color"
                                    :text-color="chip.textColor"
                                  >
                                    {{ chip.info }}
                                    <v-icon right>{{ chip.icon }}</v-icon>
                                  </v-chip>
                                </small>
                              </v-flex>
                            </v-layout>
                          </v-flex>
                          <v-flex xs1 pa-0>
                            <v-tooltip dark debounce="1" color="black" bottom>
                              <template v-slot:activator="{ on }">
                                <v-checkbox
                                  v-model="bookmarks"
                                  :value="subcat.title"
                                  multiple
                                  color="red"
                                  off-icon="favorite_border"
                                  on-icon="favorite"
                                  hint="Bookmark this link"
                                  height="0"
                                  ma-0
                                  pa-0
                                  @click.native.stop.prevent
                                  @change="handleCheckboxChange"
                                  v-on="on"
                                ></v-checkbox>
                              </template>
                              <span>Add as Favourite!</span>
                            </v-tooltip>
                          </v-flex>
                        </v-layout>
                      </v-card-text>
                    </v-card>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import data from "~/data/medical.json";
export default {
  data() {
    return {
      cardColor: "#FFF4EE",
      PageTitle: "Medical",
      initlinks: data,
      publicPath: process.env.BASE_URL
    };
  },
  computed: {
    disp() {
      let disp = JSON.parse(JSON.stringify(this.initlinks));
      return disp.map((c, index) => {
        let normalLinks = c.sublinks.filter(function(x) {
          return this.indexOf(x.title) < 0;
        }, this.bookmarks);
        let favs = c.sublinks.filter(function(x) {
          return this.indexOf(x.title) >= 0;
        }, this.bookmarks);
        c.sublinks = favs.concat(this.displayLinks(normalLinks, index));
        return c;
      });
    },
    bookmarks: {
      get() {
        return this.$store.getters.getMedicalBookmarks;
      },
      set(value) {
        this.$store.commit("setMedicalBookmarks", value);
      }
    },
    chips() {
      return this.$store.getters.getChips;
    }
  },

  methods: {
    displayLinks(a, index) {
      let initialArraySize = this.initlinks[index].sublinks.length;
      for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
      }
      return a.slice(1, 9 - (initialArraySize - a.length));
    },
    showChips() {
      //copy the chips original array
      let tempChips = JSON.parse(JSON.stringify(this.chips));
      // create a random number the larger the difference the less it will hit
      let min = 1;
      let max = 7;
      let random = Math.floor(Math.random() * (+max - +min)) + +min;
      // console.log(random);
      // shuffle chips array
      for (let i = tempChips.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [tempChips[i], tempChips[j]] = [tempChips[j], tempChips[i]];
      }
      // if random number is found then return the single chip which is shuffled
      // from the main list and only 1 is returned, else []
      return random === 5 ? tempChips.slice(0, 1) : [];
    },
    handleCheckboxChange: function(event) {
      console.log(event);
    }
  }
};
</script>
<style scoped lang="scss">
.folks_med {
  background-color: #e5f1ff;
}
</style>
